@import '../../assets/styles/mixin/mixin';
@import '../../assets/styles/variables';

.homeWrapper {
    background: #f5f6f8;

}


.parentHeader {
    padding-bottom: 60px;
    transition: 0.4s background;
}


.showMore {
    text-align: center;
    margin-top: 40px;

    .showMoreBtn {
        padding: 7px 12px;
        border-radius: 20px;
        border: none;
        background: $primary;
        color: #fff;
        font-size: 15px;
        font-weight: 600;

        svg {
            font-size: 18px;
        }
    }
}

.bannerImage {
    border-radius: 5px;
    margin-top: -45px;
    width: 100%;
}

.policy-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: stretch;
    -ms-flex-align: stretch;
    align-items: stretch;
    overflow: hidden;
    border-radius: 10px;

    p {
        position: relative;
        width: 50%;
        min-height: 1.6rem;
        padding: 10px 5px;
        font-size: 11px;
        line-height: 18px;
        margin: 0;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            content: "";
            background: -webkit-gradient(linear, right top, left top, from(#fff), to(#f4f5f7));
            background: linear-gradient(270deg, #fff, #f4f5f7);
        }

        &::after {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 20px;
            height: 20px;
            content: "";
            background: url(data:image/png;base64,);
            background-size: 100% 100%;
        }

        .text {
            position: relative;
            z-index: 1;

            strong {
                display: block;
                font-size: 14px;
                font-weight: 700;
            }
        }

        &:nth-child(2) {
            &::after {
                position: absolute;
                right: 0;
                bottom: 0;
                width: 20px;
                height: 20px;
                content: "";
                background: url(data:image/png;base64,);
                background-size: 100% 100%;
            }
        }
    }
}

.fiveNavs {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    margin: 0;
    padding: 0;
    margin-top: .533rem;

    li {
        width: 20%;
        padding: 0 .107rem;
        text-align: center;

        a {
            display: block;

            .nav-icon {
                position: relative;
                display: block;
                width: 2.173rem;
                height: 2.173rem;
                margin: 0 auto;
            }
        }

        .navTitle {
            color: #212121;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            display: inline-block;
            font-size: 12px;
        }

        .game-icon {
            background: url(data:image/png;base64,) no-repeat;
            background-size: 100% 100%;

            &::after {
                position: absolute;
                top: -0.12rem;
                right: -0.7rem;
                width: 1.3rem;
                height: 0.673rem;
                content: "";
                background: url(data:image/png;base64,) no-repeat;
                background-size: 100% 100%;
                animation: newshake-data-v-5ef7972d 3s linear infinite;
                transform: scale(1.2);
            }
        }

        .coupons-icon {
            background: url(data:image/png;base64,) no-repeat;
            background-size: 100% 100%;
        }

        .saving-icon {
            position: relative;
            background: url(data:image/png;base64,) no-repeat;
            background-size: 100% 100%;
        }

        .slash-icon {
            background: url(data:image/png;base64,) no-repeat;
            background-size: 100% 100%;
        }

        .snatch-icon {
            background: url(data:image/png;base64,) no-repeat;
            background-size: 100% 100%;
        }
    }
}

/********** Responsive ***********/
@media (max-width: 575.98px) {
    .homeWrapper {
        margin-top: 80px;
    }

    .bannerImage {
        margin-top: 10px;
    }

    .parentHeader {
        background: none !important;
        padding-bottom: 0;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .homeWrapper {
        margin-top: 80px;
    }

    .bannerImage {
        margin-top: 10px;
    }


    .parentHeader {
        background: none !important;
        padding-bottom: 0;
    }
}

